import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { hash } from 'bcryptjs';

import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

const CreateWallet = ({ onWalletCreated }) => {
  const [walletData, setWalletData] = useState(null);
  const [walletName, setWalletName] = useState('My Wallet');
  const navigate = useNavigate();
  const [password, setPassword] = useState('');

  const handleCreateWallet = async () => {
    try {
      const response = await axios.post('/api/create-wallet');
      setWalletData(response.data);

      const hashedPassword = await hash(password, 10);

      console.log('hashed pass: ', hashedPassword);
      const walletDetails = { ...response.data, walletName, hashedPassword };
      console.log(walletDetails);
      localStorage.setItem('storedWalletData', JSON.stringify(walletDetails));


    } catch (error) {
      console.error('Error creating wallet:', error);
    }
  };

  const goToDashboard = () => {
    onWalletCreated();
    navigate('/dashboard');
  }

  return (

    <Stack direction="column" spacing={2}>
      <h1 className='h1'>Create Wallet</h1>
      {/* Input for wallet name */}
      <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
        <label htmlFor="walletName" style={{ width: '120px', fontWeight: 'bold', color: 'green', marginRight: '10px' }}>
          Wallet Name:
        </label>
        <TextField
          size='small'
          onChange={(e) => setWalletName(e.target.value)}
          id="outlined-basic"
          label="Wallet Name"
          variant="outlined"
          defaultValue={walletName}
        />

      </div>

      <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
        <label htmlFor="password" style={{width: '120px', fontWeight: 'bold', color: 'green', marginRight: '10px' }}>Password:</label>
        <TextField
          size='small'
          oonChange={(e) => setPassword(e.target.value)}
          id="outlined-basic"
          type='password'
          label="Password"
          variant="outlined"
          defaultValue={password}
        />
       
      </div>

      <Button variant='contained' onClick={handleCreateWallet}>Create Wallet</Button>

      {walletData && (
        <div>
          {/*<p>Seed Phrase: {walletData.mnemonic}</p>*/}
          <p>Wallet name: {walletName}</p>
          <p>Address: {walletData.publicAddress}</p>
          <p><strong>Please store your privatekey somewhere safe and NEVER SHARE WITH ANYONE </strong></p>
          <p>Private Key: {walletData.privateKey}</p>
          <button onClick={goToDashboard}>I understand, I have stored the private key safely.</button>
        </div>
      )}
    </Stack>
  );
};

export default CreateWallet;
