//import { type } from '@testing-library/user-event/dist/type';
import React from 'react';
import { useParams } from 'react-router-dom';
import { Link } from 'react-router-dom';

const DetailedAssetView = ({ assets }) => {
    
   const { id } = useParams();
    
  
    // Use the 'id' parameter to retrieve the specific asset details
     const asset = assets.find((a) => a.id === id);
   //console.log(assets[1].id);

   if (!asset) {
    return (
      <div className="detailed-asset-view">
        <p>Asset not found</p>
      </div>
    );
  }

  return (
    <>
    <div><button onClick={() => window.history.back()}>Back to Asset List</button></div>
    <div className="detailed-asset-view">
      <h2>{asset.name} Details</h2>
      <p>Symbol: {asset.symbol}</p>
      <p>Current Price: ${asset.current_price}</p>
      <p>Quantity Held: {asset.balance}</p>

      <h3>Transaction History</h3>
      <ul>
       {/* {asset.transactionHistory.map((transaction, index) => (
          <li key={index}>
            Transaction #{index + 1}: {transaction.date} - {transaction.description}
          </li>
       ))} */}
      </ul>

      <h3> Send/Receive {asset.name}</h3>
      <button><Link to={`/send/${asset.id}`}>Send {asset.name}</Link></button>
      <button>Receive {asset.name}</button>
      
      
    </div>
    </>
  );
};

export default DetailedAssetView;
