import React from 'react';
import { Link } from 'react-router-dom';


import Table from '@mui/joy/Table';

const AssetList = ({ assets }) => {
  return (
    <div className="asset-list">
      <h3 className='h1'>Asset List</h3>
      
        
           <Table hoverRow>
           <thead>
             <tr>
               <th style={{textAlign: 'center', width: '20%'}}>Rank</th>
               <th style={{textAlign: 'center', width: '60%'}}>Name (Symbol)</th>
               <th style={{textAlign: 'center', width: '20%'}}>Price($)</th>
             </tr>
           </thead>
           <tbody>
             {assets.map((coin) => (
               <tr key={coin.id}>
                 <td>{coin.rank}</td>
                 <td><Link to={`/asset/${coin.id}`}>{coin.name}</Link> ({coin.symbol})</td>
                 <td>{Number(coin.priceUsd).toFixed(2)}</td>
               </tr>
             ))}
           </tbody>
         </Table>

      
    </div>
  );
};

export default AssetList;
