import React, { useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { hash } from 'bcryptjs';

import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

const RestoreWallet = ({ onWalletCreated }) => {

  const navigate = useNavigate();

  const [mnemonic, setMnemonic] = useState('');
  const [walletName, setWalletName] = useState('My Wallet');
  const [restoredWallet, setRestoredWallet] = useState(null);
  const [password, setPassword] = useState('');


  const handleRestore = async () => {
    try {
      const response = await axios.post('/api/restore-wallet', { mnemonic });
      setRestoredWallet(response.data);
      const hashedPassword = hash(password, 10);
      const walletDetails = { ...response.data, walletName, hashedPassword };
      //console.log(walletDetails);
      localStorage.setItem('storedWalletData', JSON.stringify(walletDetails));

    } catch (error) {
      console.error('Error restoring wallet:', error);
    }
  };
  const goToDashboard = () => {
    onWalletCreated();
    navigate('/dashboard');
  }
  return (
    <Stack direction='column' spacing={2}>
      <h2 className="h1">Restore Wallet</h2>
      <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
        <label htmlFor="walletName" style={{ width: '150px', fontWeight: 'bold', color: 'green', marginRight: '10px' }}>
          Wallet Name:
        </label>
        <TextField
          size="small"
          onChange={(e) => setWalletName(e.target.value)}
          id="outlined-basic"
          label="Wallet Name"
          variant="outlined"
          value={walletName}
        />
      </div>

      <div style={{ marginTop: '10px' }}>
        <label htmlFor="mnemonic" style={{ width: '150px', fontWeight: 'bold', color: 'green' }}>
          Mnemonic Phrase:
        </label>
        <TextField

          id="filled-multiline-static"
          label="Seed Phrase"
          multiline
          rows={4}
          defaultValue={mnemonic}
          placeholder="Enter your 12-word mnemonic phrase"
          onChange={(e) => setMnemonic(e.target.value)}
        />

      </div>

      <div style={{ display: 'flex', flexDirection: 'row', alignItems: 'center', marginTop: '10px' }}>
        <label htmlFor="password" style={{ width: '150px', fontWeight: 'bold', color: 'green', marginRight: '10px' }}>
          Password:
        </label>
        <TextField
          size="small"
          onChange={(e) => setPassword(e.target.value)}
          id="outlined-basic"
          type="password"
          label="Password"
          variant="outlined"
          defaultValue={password}
        />
      </div>

      <Button variant="contained" onClick={handleRestore}>
        Restore Wallet
      </Button>

      {restoredWallet && (
        <div>
          <p>Address: {restoredWallet.address}</p>
          <button onClick={goToDashboard}>I understand, I have stored the private key safely.</button>
        </div>
      )}
    </Stack>

  );
};

export default RestoreWallet;
