import React from "react";
import AssetList from "./AssetList";
import { Link } from "react-router-dom";


import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';

const Dashboard = (props) => {
  return (
    <Stack className="dashboard" direction='column' spacing={2} >
      <h2 className="h1">Welcome, {props.walletName}!</h2>
      {/*<p>Total Assets Value: ${props.totalAssetsValue.toFixed(2)}</p>*/}
      <Link className="link" to={`/send`}>Send</Link>
      <Link className="link" to={`/wallet/${props.walletAddress}`}>My Wallet</Link>
      <AssetList assets={props.assets} />
      {/*<CreateWallet onWalletCreated={props.handleWalletCreated} /> */}
    </Stack>
  );
};

export default Dashboard;
