import React, { useState, useEffect } from 'react';
import axios from 'axios';

const WalletDetails = ({ walletAddress }) => {
  const [walletData, setWalletData] = useState(null);
  //const walletAddress = '0x310CE502c3f1F936191D52BC812Bb322badA2A56'; // Replace with the actual wallet address

  useEffect(() => {
    // Make a GET request to the backend to fetch wallet data
    axios.get(`/api/wallet/${walletAddress}`)
      .then((response) => {
        //console.log('im here')
        setWalletData(response.data);
      })
      .catch((error) => {
        console.error('Error fetching wallet data:', error);
      });
  }, []);

  return (
    <div>
      <h2>Wallet Details</h2>
      {walletData ? (
        <div>
          <p>Address: {walletAddress}</p>
          <p>Balance: {walletData.balance} ETH</p>
          <h3>Transactions:</h3>
          <ul>
            {walletData.transactions.length > 0 ? (
              walletData.transactions.map((tx) => (
                <li key={tx.hash}>
                  <a
                    href={`https://etherscan.io/tx/${tx.hash}`}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    Hash: {tx.hash}
                    <br />
                  </a>
                  From: {tx.from}
                  <br />
                  To: {tx.to}
                  <br />
                  Value: {tx.value} ETH
                </li>
              ))
            ) : (
              <li>Nothing to show</li>
            )}

          </ul>
        </div>
      ) : (
        <p>Loading wallet data...</p>
      )}
    </div>
  );
};

export default WalletDetails;
