import React, { useState } from 'react';
import bcrypt, { hash } from 'bcryptjs';
import { useNavigate } from 'react-router-dom';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';
import SendIcon from '@mui/icons-material/Send';

import Input from '@mui/material/Input';
import FormControl from '@mui/material/FormControl';
import IconButton from '@mui/material/IconButton';
import InputLabel from '@mui/material/InputLabel';
import InputAdornment from '@mui/material/InputAdornment';
import Visibility from '@mui/icons-material/Visibility';
import VisibilityOff from '@mui/icons-material/VisibilityOff';

const PasswordVerify = ({ hashedPassword }) => {
  const [password, setPassword] = useState('');
  const [verificationResult, setVerificationResult] = useState('');
  const [verified, setVerified] = useState(false)

  const navigate = useNavigate();

  const [showPassword, setShowPassword] = React.useState(false);

  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  const verifyPassword = async () => {
    try {

      const isPasswordCorrect = await bcrypt.compare(password, hashedPassword);

      if (isPasswordCorrect) {
        setVerificationResult('Password is correct!');
        setVerified(true);
        navigate('/dashboard', { replace: true });

      } else {
        setVerificationResult('Password is incorrect. Please try again.');
      }
    } catch (error) {
      console.error('Error verifying password:', error);
    }
  };

  return (
    <Stack>

      <label className='h1' htmlFor="password">Enter Password</label>

      <FormControl sx={{ m: 1, width: '25ch' }} variant="standard">
        <InputLabel htmlFor="standard-adornment-password">Password</InputLabel>
        <Input
          onChange={handlePasswordChange}
          value={password}
          id="standard-adornment-password"
          type={showPassword ? 'text' : 'password'}
          endAdornment={
            <InputAdornment position="end">
              <IconButton
                aria-label="toggle password visibility"
                onClick={handleClickShowPassword}
                onMouseDown={handleMouseDownPassword}

              >
                {showPassword ? <VisibilityOff /> : <Visibility />}
              </IconButton>
            </InputAdornment>
          }
        />
      </FormControl>

      
      <Button size='small' className="custom-button" onClick={verifyPassword} variant="contained" endIcon={<SendIcon />}  >
        Unlock Wallet
      </Button>
      <p>{verificationResult}</p>
    </Stack>
  );
};

export default PasswordVerify;
