import { useState, useEffect } from 'react';
import Dashboard from './components/Dashboard';
import Send from './components/Send';
import './App.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import DetailedAssetView from './components/detailedAssetView';
import { fetchCryptoData } from './apis/CryptoData';
import WalletDetails from './components/WalletDetails';
import CreateWallet from './components/CreateWallet';
import LandingPage from './components/LandingPage';
import RestoreWallet from './components/RestoreWallet';

function App() {
  const [assets, setAssets] = useState([]);
  const [publicAddress, setPublicAddress] = useState(null);
  const [walletName, setWalletName] = useState('My Wallet');
  const [walletCreated, setWalletCreated] = useState(false);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const data = await fetchCryptoData();
        setAssets(data);
      } catch (error) {
        console.error('Error fetching crypto data:', error);
      }
    };

    fetchData();
  }, []);

  const clearLocalStorage = () => {
    localStorage.removeItem('storedWalletData');
  }

  // Retrieve the stored string from localStorage
  const storedWalletData = localStorage.getItem('storedWalletData');

  console.log("App.js: ",storedWalletData);
  useEffect(() => {
    if (storedWalletData) {
      const walletDetails = JSON.parse(storedWalletData);
      setPublicAddress(walletDetails.publicAddress);
      setWalletName(walletDetails.walletName);
    }

  }, [storedWalletData]);
  // Only run this effect if storedWalletData changes

  const onWalletCreated = () => {
    setWalletCreated(true);
  }

  return (


    <div className="App">

      <Router>
        <Routes>
          <Route path='/restore-wallet' element={<RestoreWallet onWalletCreated={onWalletCreated} />} />
          <Route path='/create' element={<CreateWallet onWalletCreated={onWalletCreated} />} />
          <Route path={`/wallet/${publicAddress}`} element={<WalletDetails walletAddress={publicAddress} />} />
          <Route path='/' element={<LandingPage />} />
          <Route path='/dashboard' element={<Dashboard assets={assets} walletName={walletName} walletAddress={publicAddress} />} />
          <Route path="/asset/:id" element={<DetailedAssetView assets={assets} />} />
          <Route path='/send' element={<Send assets={assets} />} />
          <Route path='/send/:id' element={<Send assets={assets} />} />
        </Routes>
      </Router>

      <button onClick={clearLocalStorage}>Clear local data</button>
    </div>
  );
}

export default App;
