import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import PasswordVerify from './PasswordVerify';
import Stack from '@mui/material/Stack';
import Button from '@mui/material/Button';



const LandingPage = () => {
  const navigate = useNavigate();

  // Check if wallet data exists in localStorage
  const storedRawData = localStorage.getItem('storedWalletData');

  if (storedRawData) {
    const storedWalletData = JSON.parse(storedRawData);
    console.log("landing page hashed pass: ", storedWalletData.hashedPassword);
    return (
      // Wallet data exists, verify user with password
      <PasswordVerify hashedPassword={storedWalletData.hashedPassword} />
    );
  }

  const handleCreateWallet = () => {
    // Handle create wallets action
    // Redirect to create wallet page
    navigate('/create');
  };

  const handleRestoreWallet = () => {
    // Handle restore wallet action
    // Redirect to restore wallet page
    navigate('/restore-wallet');
  };

  return (
    <Stack direction="column" spacing={2}>
      <h1 className= "h1" style={{
        fontSize: '2em',
        fontWeight: 'bold',
        color: '#3498db',
        textAlign: 'center',
        margin: '20px 0'
        
      }}>
        Welcome to the Crypto Wallet App
      </h1>

      <Button variant="contained" onClick={handleCreateWallet}>Create Wallet</Button>
      <Button variant="contained" color="secondary" onClick={handleRestoreWallet}>Restore Wallet</Button>
    </Stack>
  );
};

export default LandingPage;
