import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';

const Send = ({ assets }) => {
  const { id } = useParams();
  const [formData, setFormData] = useState({
    selectedAsset: '',
    amount: '',
    recipientAddress: '',
  });

  useEffect(() => {
    // Get the 'id' parameter from the URL
   // const assetId = parseInt(id); // Assuming 'id' is a string, parse it to an integer

    // Find the asset with the matching 'id' from the URL parameter
    const matchingAsset = assets.find((asset) => asset.id === id);
    
    if (matchingAsset) {
      // Set the selected asset in the form data when the component mounts
      setFormData((prevData) => ({
        ...prevData,
        selectedAsset: matchingAsset.id,
      }));
    }
  }, [id, assets]);

  const { selectedAsset, amount, recipientAddress } = formData;

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: value,
    });
  };

  const handleSend = (e) => {
    e.preventDefault();

    // Validate form inputs here (e.g., check if the amount is valid)
    // Perform the send operation (e.g., update balances, send request to backend, etc.)

    // Reset form inputs
    setFormData({
      selectedAsset: '',
      amount: '',
      recipientAddress: '',
    });

    // Optionally, navigate back to the Dashboard or show a confirmation message
  };

  return (
    <div className="send">
      <h2>Send Crypto</h2>
      <form onSubmit={handleSend}>
        <div className="form-group">
          <label>Select Asset:</label>
          <select
            name="selectedAsset"
            value={selectedAsset}
            onChange={handleInputChange}
            required
          >
            <option value="">Select an Asset</option>
            {assets.map((asset) => (
              <option key={asset.id} value={asset.id}>
                {asset.name} ({asset.symbol})
              </option>
            ))}
          </select>
        </div>
        <div className="form-group">
          <label>Amount:</label>
          <input
            type="number"
            name="amount"
            value={amount}
            onChange={handleInputChange}
            required
          />
        </div>
        <div className="form-group">
          <label>Recipient Address:</label>
          <input
            type="text"
            name="recipientAddress"
            value={recipientAddress}
            onChange={handleInputChange}
            required
          />
        </div>
        <button type="submit">Send</button>
      </form>
    </div>
  );
};

export default Send;
